body {
    background-color: #282c34;
}

.Chat {
    max-width: 728px;
    margin: 0 auto;
}

.sign-out {
    margin-bottom: 2rem;
}

.Chat header {
    background-color: #181717;
    height: 10vh;
    min-height: 50px;
    color: white;
    position: fixed;
    width: 100%;
    max-width: 728px;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    padding: 10px;
    box-sizing: border-box;
}

.Chat section.signed-in {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Chat main {
    padding: 10px;
    height: 50vh;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    background-color: var(--clr-primary);
}

.Chat form {
    margin-top: 0;
    border-top: 1px solid var(--clr-quaternary);
}

.Chat input {
    background: var(--clr-primary);
}

ul,
li {
    text-align: left;
    list-style: none;
}

p {
    max-width: 500px;
    margin-bottom: 12px;
    line-height: 24px;
    padding: 10px 20px;
    border-radius: 25px;
    position: relative;
    color: white;
    text-align: center;
}

.message {
    display: flex;
    align-items: center;
}

.sent {
    flex-direction: row-reverse;
}

.sent p {
    color: white;
    background: #0b93f6;
    align-self: flex-end;
}
.received p {
    background: #e5e5ea;
    color: black;
}

img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 2px 5px;
}
